<template>
  <div class="poinBox" id="pointerLockControls" style="margin-left: -200px;">
    <div id="blocker" ref="blocker">
        <div id="instructions" ref="instructions">
            <span style="font-size:40px"></span>
            <br />
            <br />
            (W, A, S, D = , SPACE = , MOUSE = )
        </div>
    </div>
    <div class="" id="canvasBox" ref="canvasBox"></div>
    <div class="" id="stats"></div>
    <div class="fixed flex aic" style="left: 0%;z-index: 99;bottom: 10px;">
      <el-input size="mini" v-model="numberBox" style="width: 100px;" placeholder=""></el-input>
      <button type="success" icon="el-icon-check" class="Mybtn" style="cursor: pointer;margin-left: 10px;" circle @click.stop.prevent="changeBox">change</button>
    </div>
    <video id="video" ref="videoBox" loop muted crossOrigin="anonymous" playsinline style="display:none">
    	<source src="../../../static/video/me.mp4" type='video/mp4; codecs="avc1.42E01E, mp4a.40.2"'>
    </video>
  </div>
</template>

<script>
import * as THREE from "three";
  import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
  // import 'three/examples/js/controls/FirstPersonControls.js';
  import { FirstPersonControls } from 'three/examples/jsm/controls/FirstPersonControls.js';
  import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js';
  // import { PointerLockControls } from './pointer.js';
  export default{
    data(){
      return {
        camera:null,
        scene:null,
        renderer:null,
        controls:null,
        objects : [],
        raycaster:null,
        raycasterBox:null,
        horizontalRaycaster : null,//
        moveForward : false,
        moveBackward : false,
        moveLeft : false,
        moveRight : false,
        canJump : false,
        prevTime : performance.now(),
        velocity : new THREE.Vector3(),
        direction : new THREE.Vector3(),
        vertex : new THREE.Vector3(),
        color : new THREE.Color(),
        rotation : new THREE.Vector3(), //
        speed : 500, //
        upSpeed : 1000, //
        group : new THREE.Group(),
        videoBox:null,
        sphere:null,
        floor1:null,
        pointerLine:null,
        pointerLine1:null,
        boxLingthGroud:null,
        numberBox:2,
        numberBoxList:[],
        Animation:null,
        uniforms1:null,
        clock:null
      }
    },
    watch:{

    },
    mounted() {
      // this.blocker = this.$refs.blocker
      // this.instructions = this.$refs.instructions
      // this.canvasBox = this.$refs.canvasBox
      // this.stats = this.$refs.stats
      this.init();
      this.camera.position.set(1200,0,1200)
      this.camera.lookAt(0,0,0)
      this.animate();
    },
    beforeDestroy() {
      cancelAnimationFrame(this.Animation)
    },
    methods:{
      changeBox(){
        var allChildren = this.scene.children;
        for (var i = allChildren.length - 1; i >= 0; i--) {
          if (allChildren[i] instanceof THREE.Group) {
            this.scene.remove(allChildren[i]);
          }
        }
        this.boxLingthGroud=null
        this.addRo()
      },
      init() {
        let that=this
        this.clock=new THREE.Clock()
        this.videoBox=this.$refs.videoBox

      	this.camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 1, 30000 );
      	this.camera.position.y = 10;

      	this.scene = new THREE.Scene();
      	this.scene.background = new THREE.Color( '#101010' );
      	// this.scene.background = new THREE.Color( '#dedede' );
      	// this.scene.fog = new THREE.Fog( 0xffffff, 0, 750 );

      	var light = new THREE.HemisphereLight( '#9a9aa5', '#474751', 0.75 );
      	light.position.set( 0.5, 1, 0.75 );
      	this.scene.add( light );
        var dirLight = new THREE.DirectionalLight( 0xffffff, 0.05 );
        dirLight.position.set( 0, - 1, 0 ).normalize();
        dirLight.color.setHSL( 0.5, 0.7, 0.5 );
        this.scene.add( dirLight );

      	//
        this.initControls()

      	//
        this.onKeyKnow()

        //
      	this.raycaster = new THREE.Raycaster( new THREE.Vector3(), new THREE.Vector3( 0, -1, 0 ), 0, 10 );
      	this.raycasterBox = new THREE.Raycaster(new THREE.Vector3(), new THREE.Vector3(), 0, 10);
      	this.horizontalRaycaster = new THREE.Raycaster(new THREE.Vector3(), new THREE.Vector3(), 0, 10);

      	//
        this.addFloor()
        this.addFloor1()

        //OBJ
        this.addObj()

      	//
        this.addRo()

        this.addCylinder()

      	this.renderer = new THREE.WebGLRenderer( { antialias: true } );
      	this.renderer.setPixelRatio( window.devicePixelRatio );
        // debugger
      	this.renderer.setSize( this.$refs.canvasBox.clientWidth+370, window.innerHeight );
      	this.$refs.canvasBox.appendChild( this.renderer.domElement );

      	//

      	window.addEventListener( 'resize', this.onWindowResize, false );

      },
      onKeyKnow(){
        let that=this
        var onKeyDown = function ( event ) {

        	switch ( event.keyCode ) {

        		case 38: // up
        		case 87: // w
        			that.moveForward = true;
        			break;

        		case 69: // e
        			that.moveForward = true;
              that.speed=5000;
        			break;

        		case 37: // left
        		case 65: // a
        			that.moveLeft = true;
        			break;

        		case 40: // down
        		case 83: // s
        			that.moveBackward = true;
        			break;

        		case 39: // right
        		case 68: // d
        			that.moveRight = true;
        			break;

        		case 32: // space
        			if ( that.canJump === true ) that.velocity.y += that.upSpeed;
        			that.canJump = false;
        			break;

        	}

        };

        var onKeyUp = function ( event ) {

        	switch ( event.keyCode ) {

        		case 38: // up
        		case 87: // w
        			that.moveForward = false;
        			break;

            case 69: // e
            	that.moveForward = false;
              that.speed=500;
            	break;

        		case 37: // left
        		case 65: // a
        			that.moveLeft = false;
        			break;

        		case 40: // down
        		case 83: // s
        			that.moveBackward = false;
        			break;

        		case 39: // right
        		case 68: // d
        			that.moveRight = false;
        			break;

        	}

        };

        document.addEventListener( 'keydown', onKeyDown, false );
        document.addEventListener( 'keyup', onKeyUp, false );
      },
      initControls(){
        let that=this
        this.controls = new PointerLockControls( this.camera, this.$refs.canvasBox );

        var blocker = this.$refs.blocker;
        var instructions = this.$refs.instructions;

        if(this.$route.params.type==2){
          setTimeout(function(){
            var e = document.createEvent("MouseEvents");
            e.initEvent("click", true, true);
            instructions.dispatchEvent(e);
          },100)
        }

        instructions.addEventListener( 'click', function () {

        	that.controls.lock();
          // instructions.style.display = 'none';
          // blocker.style.display = 'none';

        }, false );

        this.controls.addEventListener( 'lock', function () {

          that.videoBox.muted=false;
          console.log(222)
        	instructions.style.display = 'none';
        	blocker.style.display = 'none';

        } );

        this.controls.addEventListener( 'unlock', function () {
          console.log(2229)
          // debugger
          // that.videoBox.paused=true;
          that.videoBox.muted=true;
        	blocker.style.display = 'block';
        	instructions.style.display = '';

        } );

        this.scene.add( this.controls.getObject() );
      },
      // floor
      addFloor(){
        let that=this
        // floor
        this.uniforms1 = {
        	"time": { value: 2.0 },
          // "color":{value:new THREE.Color("#dfgdfd")}
        };

        var floorGeometry = new THREE.PlaneBufferGeometry( 2000, 2000, 100, 100 );
        floorGeometry.rotateX( - Math.PI / 2 );

        // let video=document.createElement('video');
        // video.src="../../../static/video/me.mp4";
        // video.autoplay='autoplay';
        // video.crossOrigin='anonymous';
        // // video.muted='muted';
        // video.loop='loop';
        this.videoBox.play();
        var texture=new THREE.VideoTexture(this.videoBox);


        // vertex displacement

        var position = floorGeometry.attributes.position;

        for ( var i = 0, l = position.count; i < l; i ++ ) {

        	this.vertex.fromBufferAttribute( position, i );

        	this.vertex.x += Math.random() * 20 - 10;
        	this.vertex.y += Math.random() * 2;
        	this.vertex.z += Math.random() * 20 - 10;

        	position.setXYZ( i, this.vertex.x, this.vertex.y, this.vertex.z );

        }

        floorGeometry = floorGeometry.toNonIndexed(); // 

        position = floorGeometry.attributes.position;
        var colors = [];

        for ( var i = 0, l = position.count; i < l; i ++ ) {

        	this.color.setHSL( Math.random() * 0.6 + .2, Math.random() * 0.25 + 0.3, Math.random() * 0.25 + 0.2 );
          // console.log(Math.random() * 0.6 + 1, 0.75, Math.random() * 0.25 + 0.75)
        	colors.push( this.color.r, this.color.g, this.color.b );

        }

        floorGeometry.setAttribute( 'color', new THREE.Float32BufferAttribute( colors, 3 ) );

        // var floorMaterial = new THREE.MeshBasicMaterial( {
        //   vertexColors: true,
        //   // map: texture, // 
        // } );
        var floorMaterial = new THREE.ShaderMaterial( {
           // vertexColors: true,
        	uniforms: that.uniforms1,
        	vertexShader: document.getElementById( 'vertexShader' ).textContent,
        	fragmentShader: document.getElementById( 'fragment_shader1' ).textContent

        } );

        var floor = new THREE.Mesh( floorGeometry, floorMaterial );
        // this.scene.add( floor );
      },
      // floor
      addFloor1(){

        // var floorGeometry = new THREE.BoxBufferGeometry( 2000, 2000, 10,100,100 );
        var floorGeometry = new THREE.PlaneBufferGeometry( 2000, 2000, 100,50 );

        // floorGeometry=floorGeometry.toNonIndexed();

        floorGeometry.rotateX( - Math.PI / 2 );
        // floorGeometry.translateZ(1000)

        // let video=document.createElement('video');
        // video.src="../../../static/video/me.mp4";
        // video.autoplay='autoplay';
        // video.crossOrigin='anonymous';
        // // video.muted='muted';
        // video.loop='loop';
        this.videoBox.play();
        var texture=new THREE.VideoTexture(this.videoBox);


        // vertex displacement

        var position = floorGeometry.attributes.position;

        for ( var i = 0, l = position.count; i < l; i ++ ) {

        	this.vertex.fromBufferAttribute( position, i );

        	this.vertex.x += Math.random() * 20 - 10;
        	this.vertex.y += Math.random() * 2;
        	this.vertex.z += Math.random() * 20 - 10;

        	position.setXYZ( i, this.vertex.x, this.vertex.y, this.vertex.z );

        }

        floorGeometry = floorGeometry.toNonIndexed(); // 

        position = floorGeometry.attributes.position;
        var colors = [];

        for ( var i = 0, l = position.count; i < l; i ++ ) {

        	this.color.setHSL( Math.random() * 1 + 60, 300, Math.random() * 1 + 0 );
        	colors.push( this.color.r, this.color.g, this.color.b );

        }

        floorGeometry.setAttribute( 'color', new THREE.Float32BufferAttribute( "colors", 3 ) );

        var floorMaterial = new THREE.MeshPhysicalMaterial( {
          vertexColors: true,
          // map: texture, // 
        } );

        var floor = new THREE.Mesh( floorGeometry, floorMaterial );
        // floor.translateY(1500)
        this.floor1=floor
        this.scene.add( floor );
      },
      //obj
      addObj(){
        // objects

        var boxGeometry = new THREE.BoxBufferGeometry( 20, 20, 20 );
        boxGeometry = boxGeometry.toNonIndexed(); // 

        var position = boxGeometry.attributes.position;
        var colors = [];

        for ( var i = 0, l = position.count; i < l; i ++ ) {

        	this.color.setHSL( Math.random() * 0.3 + 0.5, 0.75, Math.random() * 0.25 + 0.75 );
        	colors.push( this.color.r, this.color.g, this.color.b );

        }

        boxGeometry.setAttribute( 'color', new THREE.Float32BufferAttribute( colors, 3 ) );

        var texture=new THREE.VideoTexture(this.videoBox);
        // var texture=new THREE.VideoTexture(video);

        for ( var i = 0; i < 500; i ++ ) {

        	var boxMaterial = new THREE.MeshPhongMaterial( { specular: 0xffffff, flatShading: true, vertexColors: true,map:texture} );
        	boxMaterial.color.setHSL( Math.random() * 0.2 + 0.5, 0.75, Math.random() * 0.25 + 0.75 );

        	var box = new THREE.Mesh( boxGeometry, boxMaterial );

          // let pointerLine= new THREE.PointLight( '#fa0004', 1, 1000 );
          // pointerLine.add(box)

        	box.position.x = Math.floor( Math.random() * 20 - 10 ) * 100;
        	box.position.y = Math.floor( Math.random() * 20 ) * 20 + 20;
        	box.position.z = Math.floor( Math.random() * 20 - 10 ) * 100;

        	this.scene.add( box );
        	// this.scene.add( pointerLine );
        	this.objects.push( box );

        }
      },
      addRo(){
        let sphere2,pointerLine2;

        this.boxLingthGroud=new THREE.Group()
        this.numberBox=40
        for(let i =0;i<this.numberBox;i++){
          var r = this.random(0, 256),g = this.random(0, 256),b = this.random(0, 256);
          sphere2 = new THREE.Mesh( new THREE.SphereGeometry(5,20,20), new THREE.PointsMaterial( {color: "rgb("+ r +","+ g +","+ b +")"} ) );
          pointerLine2=new THREE.PointLight( "rgb("+ r +","+ g +","+ b +")", .9, 1000 )
          pointerLine2.add(sphere2)
          // pointerLine2.position.y=10
          this.boxLingthGroud.add(pointerLine2)
          this.numberBoxList.push(Number((Math.random() * (0 + 1)).toFixed(2)))
        }
        console.log(this.numberBoxList)
        this.scene.add(this.boxLingthGroud)
      },
      //
      addCylinder(){
        let cylinderGeo = new THREE.CylinderGeometry(10,10,1000),
        material = new THREE.MeshPhongMaterial({
          color:'#ffffff'
        }),
        cylinder = new THREE.Mesh(cylinderGeo, material)
        cylinder.position.y = 500
        this.scene.add(cylinder)

      },
      random(lower, upper) {
      	return Math.floor(Math.random() * (upper - lower)) + lower;
      },
      onWindowResize() {

      	this.camera.aspect = window.innerWidth / window.innerHeight;
      	this.camera.updateProjectionMatrix();
      	this.renderer.setSize( window.innerWidth, window.innerHeight );

      },
      animate() {
        
        let that=this
      	this.Animation = requestAnimationFrame( that.animate );
      	if ( this.controls.isLocked === true ) {
          this.uniforms1[ "time" ].value += (this.clock.getDelta()) * 5;

      		var time = performance.now();
      		var delta = ( time - that.prevTime ) / 1000;

          var timeLingth = Date.now() * 0.00025;
          var d = 100;
          for(let i=0;i< this.boxLingthGroud.children.length;i++){
            // this.boxLingthGroud.children[i].position.x = Math.sin( timeLingth * ((i+5)/10) ) * d;
            // this.boxLingthGroud.children[i].position.z = Math.cos( timeLingth * ((i+2)/10) ) * d;
            // this.boxLingthGroud.children[i].position.y = Math.cos( timeLingth * ((i+4)/10) ) * d<15?15:Math.cos( timeLingth * ((i+4)/10) ) * d;

            this.boxLingthGroud.children[i].position.x = Math.sin( timeLingth * (this.numberBoxList[i]+0.1) ) * d;
            this.boxLingthGroud.children[i].position.z = Math.cos( timeLingth * (this.numberBoxList[i]+0.3) ) * d;
            this.boxLingthGroud.children[i].position.y = Math.sin( timeLingth * (this.numberBoxList[i]+0.2) ) * d < 8 ? 8 : Math.sin( timeLingth * (this.numberBoxList[i]+0.2) ) * d;
            // this.boxLingthGroud.children[i].position.y = Math.tan( timeLingth * (this.numberBoxList[i]+0.2) ) * d;
            // this.boxLingthGroud.children[i].position.y = 2.5;
          }
          // this.camera.position.set(this.boxLingthGroud.children[0].position.x, this.boxLingthGroud.children[0].position.y, this.boxLingthGroud.children[0].position.z);
          // this.camera.lookAt(new THREE.Vector3(this.boxLingthGroud.children[1].position.x+1, this.boxLingthGroud.children[1].position.y+1, this.boxLingthGroud.children[1].position.z+1));
          // this.camera.lookAt(10, 10, 10);
          // console.log(this.boxLingthGroud.children[1].position.x, this.boxLingthGroud.children[1].position.y, this.boxLingthGroud.children[1].position.z)
          
      		this.velocity.x -= this.velocity.x * 10.0 * delta;
      		this.velocity.z -= this.velocity.z * 10.0 * delta;

      		this.velocity.y -= 9.8 * 100.0 * delta; // 100.0 = mass
      		this.direction.z = Number( this.moveForward ) - Number( this.moveBackward );
      		this.direction.x = Number( this.moveRight ) - Number( this.moveLeft );

      		this.direction.normalize(); // this ensures consistent movements in all directions

          // this.group.position.set(this.control.position.x,this.control.position.y,this.control.position.z);

          //
          var control = this.controls.getObject();

      		//
          this.rotation.copy(control.getWorldDirection(new THREE.Vector3()).multiply(new THREE.Vector3(1, 1, 1)));

          //
          var m = new THREE.Matrix4();
          if(this.direction.z > 0){
              if(this.direction.x > 0){
                  m.makeRotationY(Math.PI/4);
              }
              else if(this.direction.x < 0){
                  m.makeRotationY(-Math.PI/4);
              }
              else{
                  m.makeRotationY(0);
              }
          }
          else if(this.direction.z < 0){
              if(this.direction.x > 0){
                  m.makeRotationY(Math.PI/4*3);
              }
              else if(this.direction.x < 0){
                  m.makeRotationY(-Math.PI/4*3);
              }
              else{
                  m.makeRotationY(Math.PI);
              }
          }
          else{
              if(this.direction.x > 0){
                  m.makeRotationY(Math.PI/2);
              }
              else if(this.direction.x < 0){
                  m.makeRotationY(-Math.PI/2);
              }
          }
          //
          // this.rotation.applyMatrix4(m);
          // //horizontal.setDirection(this.rotation);
          // this.horizontalRaycaster.set( control.position , this.rotation );

          // var horizontalIntersections = this.horizontalRaycaster.intersectObjects( this.scene.children, true );
          // var horOnObject = horizontalIntersections.length > 0;
          // //
          // // horizontalIntersections = this.horizontalRaycaster.intersectObjects( this.boxLingthGroud.children, true );
          // if(horizontalIntersections.length>0){
          //   console.log('222')
          //   // this.$router.push({name:'curve'})
          // }

          // //
          // if(!horOnObject){
              if ( this.moveForward ) this.velocity.z -= this.direction.z * this.speed * delta;
              if ( this.moveBackward ) this.velocity.z -= this.direction.z * this.speed * delta;
              if ( this.moveLeft || this.moveRight ) this.velocity.x -= this.direction.x * this.speed * delta;
          // }

          //
          this.raycaster.ray.origin.copy( that.controls.getObject().position );
          this.raycaster.ray.origin.y -= 10;

          //.intersectObject
          var intersections = this.raycaster.intersectObjects( that.objects,true );

          var onObject = intersections.length > 0;
          // console.log(this.raycaster.intersectObjects( [this.floor1],true ))
          // console.log(this.velocity.y)
          //
      		if ( onObject === true || this.raycaster.intersectObject( this.floor1,true ).length>0) {

      			this.velocity.y = Math.max( 0, this.velocity.y );
      			// this.velocity.y = 0;
      			this.canJump = true;

      		}

      		this.controls.moveRight( - this.velocity.x * delta );
      		this.controls.moveForward( - this.velocity.z * delta );

          //
          // control.translateX( this.velocity.x * delta );
          // control.translateY( this.velocity.y * delta );
          // control.translateZ( this.velocity.z * delta );

      		this.controls.getObject().position.y += ( this.velocity.y * delta ); // new behavior

      		if ( this.controls.getObject().position.y < 10 ) {

      			this.velocity.y = 0;
      			this.controls.getObject().position.y = 10;

      			this.canJump = true;

          }
        


      		this.prevTime = time;

      	}

      	this.renderer.render( this.scene, this.camera );

      },
    }
  }
</script>

<style lang="less" scoped>
  #blocker {
  	position: absolute;
  	width: 100vw;
  	height: 100%;
  	background-color: rgba(0,0,0,0.5);
  }

  #instructions {
  	width: 100vw;
  	height: 100%;

  	display: -webkit-box;
  	display: -moz-box;
  	display: box;

  	-webkit-box-orient: horizontal;
  	-moz-box-orient: horizontal;
  	box-orient: horizontal;

  	-webkit-box-pack: center;
  	-moz-box-pack: center;
  	box-pack: center;

  	-webkit-box-align: center;
  	-moz-box-align: center;
  	box-align: center;

  	color: #ffffff;
  	text-align: center;
  	font-family: Arial;
  	font-size: 14px;
  	line-height: 24px;

  	cursor: pointer;
  }
</style>



